.publica-titu{
    font-family: 'Titillium Web', sans-serif;
    font-size: 35px;
    letter-spacing: .5;
    font-weight: 600;
    line-height: 37px;
}

p{
    font-family: 'Titillium Web', sans-serif;
    color:#454545;
    font-size: 18px;
    line-height: 20px;
    font-weight:500; 
}

p.publica-tags{
    font-size: 12px;
    font-weight: 800;
    line-height: 14px;
    text-transform: uppercase;
}

a.publica-links{
    font-family: 'Titillium Web', sans-serif;
    padding: 0px 10px;
    font-weight: 400;
    border: 2px solid black;
    background: white;
    position: relative;
    font-size: 14px;
    -webkit-transition: .2s;
    -o-transition: .2s;
    transition: .2s;
}

.publica-links:hover{
    background: black !important;
    color:white;
    text-decoration: none;
    -webkit-transition:  .5s;
    -o-transition:  .5s;
    transition:  .5s;
    border: 2px solid white;
}

.publica-fecha{
    font-family: 'Titillium Web', sans-serif;
    font-size: 16px;
    font-weight: 200;
    line-height: 18px;
}

.publi-linkexterno{
    font-weight: 800;
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s;
}

.publi-linkexterno:hover{
    color:gray;
    text-decoration: none;
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s;
}

@media screen and (max-width: 576px) {
    .mover{
        margin-top:0px;
        margin-bottom:17px !important;
    }
}




