.maxancho{
    margin: 0px auto;
    color: black;
    font-family: 'Titillium Web', 'sans-serif';
}

.nav-link{
    position: relative;
    top: 0px;
}

.wrapLinks{
    padding:5px;
    border:1px solid black;
}

.activo{
    color:gray !important;
    font-size:15px;
}

.maxancho a{
    color: white;
    font-size: 13px;
    font-weight: 600;
    background: black;  
    margin: 0px 78px;
    text-transform: uppercase;
    text-align: center;
}

.nav-link:focus{
    outline: none;
}

.maxancho a:hover{
    text-decoration: none;
    color:red;
}

/* Tamaño pequeño */
@media screen and (max-width: 576px) {
    .maxancho a{
        color: white;
        font-size: 13px;
        font-weight: 600;
        background: black;  
        margin: 0px 78px;
        text-transform: uppercase;
        text-align: center;
        margin-top:5px;
    }
}


