p{
    color: black;                
    font-size: 22px;                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                
    font-family: 'Titillium Web', 'sans-serif';
    -ms-flex-preferred-size: 100%;
        flex-basis: 100%;
    font-weight: 300;
    line-height: 26px;
}

.corroIzquierda{
    padding-left: 105px;
}

.titu-puplica{
    margin-top: -20px;
    padding-top: 0px;
    margin-left: 99px;
}

h1{
    color: black;
    font-size: 90px;
    font-family: 'Titillium Web', 'sans-serif';
    -ms-flex-preferred-size: 100%;
        flex-basis: 100%;
    font-weight: 900;
    line-height: 75px;
    text-transform: uppercase;
}

.bloqueInfo{
    margin-top:24px;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    cursor:default;
}

a{
    color:black;
}

.links{
    padding: 0px 10px;
    font-weight: 400;
    border: 2px solid black;
    background: white;
    position: relative;
    top: 0px;
    font-size: 16px;
    -webkit-transition: .2s;
    -o-transition: .2s;
    transition: .2s;
    left: 0px;
}

.links:hover{
    background: black !important;
    color:white;
    text-decoration: none;
    -webkit-transition:  .5s;
    -o-transition:  .5s;
    transition:  .5s;
    border: 2px solid white;
}

.anegro{
    font-weight:600;
}

/* Tamaño grande */
@media screen and (max-width: 992px) {
    h1{
        color: black;
        font-size: 61px;
        font-family: 'Titillium Web', 'sans-serif';
        -ms-flex-preferred-size: 100%;
        flex-basis: 100%;
        font-weight: 900;
        line-height: 48px;
        text-transform: uppercase;
    }
    .titu-puplica{
        margin-top: -20px;
        padding-top: 0px;
        margin-left: 0px;
    }
    .corroIzquierda{
        padding-left: 0px;
    }
}

/* Tamaño medianos */
@media screen and (max-width: 720px) {
    h1{
        color: black;
        font-size: 61px;
        font-family: 'Titillium Web', 'sans-serif';
        -ms-flex-preferred-size: 100%;
        flex-basis: 100%;
        font-weight: 900;
        line-height: 48px;
        text-transform: uppercase;
    }
    .titu-puplica{
        margin-top: -20px;
        padding-top: 0px;
        margin-left: 0px;
    }
    .corroIzquierda{
        padding-left: 0px;
    }
}

/* Tamaño pequeño */
@media screen and (max-width: 576px) {
    h1{
        color: black;
        font-size: 36px;
        font-family: 'Titillium Web', 'sans-serif';
        -ms-flex-preferred-size: 100%;
            flex-basis: 100%;
        font-weight: 900;
        line-height: 28px;
        text-transform: uppercase;
    }
    .titu-puplica{
        margin-top: -20px;
        padding-top: 6px;
        padding-left: 0px !important;
        margin-right: 0px;
    }
    .links{
        padding: 0px 5px;
        font-weight: 400;
        border: 2px solid black;
        background: white;
        position: relative;
        top: 0px;
        font-size: 14px;
        -webkit-transition: .2s;
        -o-transition: .2s;
        transition: .2s;
        left: 0px;
    }

}



