.foo-twitter{
    background: url(/img/redes/twitter.png) no-repeat top center !important;
    background-size: contain !important;
    -webkit-transform:scale(1);
        -ms-transform:scale(1);
            transform:scale(1);
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
}

