.link-foo{
    cursor:pointer;
    margin:0px 0px;
}

.foo-img{
    width: 50px;
    height: 50px;
    margin:0px 22px;
    background: black;
}

.foo-env{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

.foo-twitter:hover, .foo-bitbucket:hover, .foo-gmail:hover,.foo-github:hover,.foo-bandcamp:hover  {
    -webkit-transform: scale(1.5);
        -ms-transform: scale(1.5);
            transform: scale(1.5);
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s;
}



/* Tamaño pequeño */
@media screen and (max-width: 576px) {
    .link-foo{
        cursor:pointer;
        margin:0px -10px;
    }
    .foo-img{
        width:40px;
        height:40px;
    }
}










