.tituPrincipal{
    font-family: 'Titillium Web', sans-serif;
    color:black;
}

.tituPrincipal p{
    font-size: 16px;
    font-weight: 400;
    line-height: 18px;
    padding-top: 14px;
}

.tituPrincipal h4{
    font-size: 22px;
    font-weight: 600;
    line-height: 10px;
    margin-top: -10px;
    line-height: 22px;
}

.logo{
    background: url(/img/logo.png) no-repeat top left;
    width:89px;
    height: 73px;
    -webkit-transition: 0.2s;
    -o-transition: 0.2s;
    transition: 0.2s;
}

.logo:hover{
    background: url(/img/logo.gif) no-repeat top left;
    width:89px;
    height: 73px;
    -webkit-transition: 0.2s;
    -o-transition: 0.2s;
    transition: 0.2s;
}


